.page_container {
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    min-height: 100%;
    min-width: 320px;
    background: url('../../images/background.jpg') no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.page_content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 0 auto;
}

.input_container_top {
    display: flex;
    flex: 1 0 auto;
    height: 100%;
    position: relative;
}

.input_container_bottom {
    display: flex;
    flex: 1 0 auto;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url('../../images/background.jpg') no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    position: relative;
}

.insufficient_funds_error {
    font-weight: bold;
    background-color: rgb(249, 17, 85);
    padding: 2px 4px;
    border-radius: 2px;
    opacity: 0.8;
}

.input_block_bottom_currency {
    opacity: 0.4;
}
